html {
  height: 100%;
}

body {
  font-family: 'Open Sans';
  color: white;
  min-height: 100%;
  margin: 0px;
  background: -webkit-radial-gradient(#2b1339, #380c77);
  background: radial-gradient(#2b1339, #380c77);
}

body.landing {
  background: #5B5579;
}

.hero-content {
  background: #271d57;
  padding: 100px 0;
}

.hero-content h1 {
  font-size: 70px;
  margin-bottom: 50px;
  font-weight: 300;
}

.selling-points {
  padding: 100px 0;
}

.selling-points .point {
  width: 32%;
  float: left;
  padding: 0 0.5%;
}

.selling-points .point h5 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
  height: 85px;
}

.selling-points .point p {
  font-size: 14px;
}

.player-header-nav {
  background-color: rgba(123, 70, 163, .9);
}

body.landing .navbar {
  margin-bottom: 0;
}

.album-header-container {
  margin-top: 55px;
  margin-bottom: 40px;
}


.album-header-information {
  margin-top: 45px;
  margin-bottom: 20px;
}

.album-header-information h3, .album-header-information h4, .album-header-information h5 {
  line-height: 150%;
}

.album-header-information h3 {
  font-size: 48px;
  font-weight: 300;
}

.album-header-information h4 {
  font-size: 24px;
  font-weight: 700;
}

.album-header-information h5 {
  font-size: 16px;
  font-weight: 500;
}

.table.album-song-listing { 
  font-weight: 300;
}

.table-album-song-listing td {
  padding: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.4);
}